import React from 'react'
import Link from 'gatsby-link'
import Marquee from 'marquee-react-dwyer'
import LocalMarquee from './marquee'

const IndexPage = () => (
  <div>
    <h1>Hi Node Folks!</h1>
    <p>Here are a few examples of the NPM Package Marquee React Dwyer (marque-react-dwyer). If you need a horizontal marquee for a React application look no further! Check out the examples, then go to NPM or GitHub to learn how easy it is to use this Marquee!</p>
    
    <h3>Example 1</h3>
    <hr/>
    <Marquee
      Size={"h1"}
      NumberOfOptions={"9"}
      Index0={"I am 1!"}
      Index1={"I am 2!"}
      Index2={"I am 3!"}
      Index3={"I am 4!"}
      Index4={"I am 5!"}
      Index5={"I am 6!"}
      Index6={"I am 7!"}
      Index7={"I am 8!"}
      Index8={"I am 9!"}
      TimeToCross={"15000"}
      TimeToChange={"2500"}
      IsRandom={"false"}
      Color={"red"}
    />
    
    <h3>Example 2</h3>
    <hr/>
    <Marquee
      Size={"h4"}
      NumberOfOptions={"3"}
      Index0={"Welcome"}
      Index1={" to "}
      Index2={"the World!"}
      TimeToCross={"8000"}
      TimeToChange={"4000"}
      IsRandom={"false"}
      Color={"green"}
    />
    
    <h3>Example 3</h3>
    <hr/>
    <Marquee
      Size={"h2"}
      NumberOfOptions={"5"}
      Index0={"Totally"}
      Index1={"Random"}
      Index2={"A"}
      Index2={"B"}
      Index2={"C"}
      TimeToCross={"20000"}
      TimeToChange={"2000"}
      IsRandom={"true"}
      Color={"purple"}
    />
    <br/>
    <p>I built this way cool example site with Gatsby. Gatsby makes React fast and easy! <a href="https://www.gatsbyjs.org/">Check them out</a></p>
  </div>
)

export default IndexPage



